html {
  margin: 0;
}
* {
  box-sizing: border-box;
}
body {
  width: 100%;
  min-height: 100vh;
  font-family: "Kumbh Sans", sans-serif;
}
.App {
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 68.75rem;
  width: 100%;
}
nav {
  width: 100%;
  height: 6rem;
  border-bottom: 4px solid hsl(223, 64%, 98%);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.nav-left {
  min-width: 34.375rem;
  width: 60%;
  display: flex;
  justify-content: space-between;
}
.nav-right {
  min-width: 6.875rem;
  width: 10%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav-links {
  padding: 0;

  margin: 0;
  width: 65%;
  color: hsl(219, 9%, 45%);
  display: flex;
  flex-direction: row;
  list-style: none;
  justify-content: space-between;
  align-items: center;
}
.nav-link:hover {
  color: hsl(220, 13%, 13%);
  cursor: pointer;
}
.nav-link {
  position: relative;
}

.nav-link:hover .nav-bottom-border {
  width: 100%;
  top: 3.2rem;
  position: absolute;
  border-bottom: 5px solid hsl(26, 100%, 55%);
}
.black-icon {
  user-select: none;
  cursor: pointer;
}
.black-icon:hover path {
  fill: hsl(220, 13%, 13%);
}
.logo {
  width: 10rem;
  object-fit: contain;
}
.avatar {
  cursor: pointer;
  width: 3rem;
  border-radius: 50%;
  border: 2px solid transparent;
}

.avatar:hover {
  border-color: hsl(26, 100%, 55%);
}
.lector {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.view {
  width: 70%;
  object-fit: contain;
  border-radius: 4%;
  cursor: zoom-in;
}
main {
  display: flex;
  flex-direction: row;
  padding-top: 3rem;
}
.thumbnails {
  padding-top: 1.3rem;
  width: 70%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.thumbnails img,
.light-thumbnails img {
  width: 100%;
  display: block;
}

.thumbnail {
  overflow: hidden;
  border-radius: 10%;
  cursor: pointer;
  width: 20%;
  border: 2px solid transparent;
  position: relative;
}
.thumbnail.select {
  border-color: hsl(26, 100%, 55%);
}
.border {
  border: 2px solid transparent;
  position: relative;
}
.thumbnail.select .border {
  border-color: hsl(26, 100%, 55%);
}
.thumbnail:hover:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(
    255,
    255,
    255,
    0.7
  ); /* Color blanco casi transparente */
  z-index: 1; /* Asegura que el overlay est√© por encima del contenido */
}

.light-view {
  width: 45vw;
  object-fit: contain;
  border-radius: 5%;
  max-width: 26rem;
  margin: auto;
  margin-top: 2rem;
}
.overlay {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  opacity: 0.75;
  background-color: black;
  z-index: 1;
}
.light-box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 2;
}
.light-thumbnails {
  padding-top: 1.3rem;
  display: flex;
  flex-direction: row;
  max-width: 23.0625rem;
  justify-content: space-between;
  margin: auto;
}
.close {
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
}
.close:hover path {
  fill: hsl(26, 100%, 55%);
}
.arrow {
  user-select: none;
  cursor: pointer;
  position: absolute;
  background-color: white;
  height: 3rem;
  width: 3rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  top: min(13.5rem, 24vw);
}
.arrow:hover path {
  stroke: hsl(26, 100%, 55%);
}
.next svg {
  transform: translateX(0.1rem);
}
.prev svg {
  transform: translateX(-0.1rem);
}
.next {
  right: -1.5rem;
}
.prev {
  left: -1.5rem;
}

.details {
  padding-top: 3rem;
  display: flex;
  flex-direction: column;
  width: 45%;
}
.orange {
  color: hsl(26, 100%, 55%);
  font-weight: 700;
  margin-bottom: 1rem;
  font-size: 0.9rem;
}
.tittle {
  font-weight: 700;
  font-size: 2.5rem;
  margin-bottom: 1rem;
}
p {
  margin: 0;
}
.desc {
  color: hsl(219, 9%, 45%);
  margin-bottom: 1rem;
}
.price-box {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.price {
  font-weight: 700;
  font-size: 1.75rem;
}
.discount {
  margin-left: 0.5rem;
  border-radius: 5px;
  font-weight: 700;
  padding-top: 0.1rem;
  display: flex;
  justify-content: center;
  color: hsl(26, 100%, 55%);
  text-align: center;
  width: 3rem;
  height: 1.5rem;
  background-color: hsl(25, 100%, 94%);
}
.discount-off {
  text-decoration: underline;
  text-underline-offset: -0.25em;
  text-decoration-skip-ink: none;
  text-decoration-color: hsl(220, 14%, 75%);
  text-decoration-thickness: 1px;
  color: hsl(220, 14%, 75%);
  font-weight: 700;
  color: hsl(220, 14%, 75%);
  margin-bottom: 2rem;
}

.count-box {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
  width: 10rem;
  height: 3rem;
  background-color: hsl(223, 64%, 98%);
}
.count {
  text-align: center;
  min-width: 3rem;
  font-weight: 700;
}
.btn-count {
  user-select: none;
  width: 3rem;
  height: 100%;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.buy-box {
  display: flex;
  flex-direction: row;
}
button {
  height: 3rem;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-weight: 700;
  box-shadow: 0 0 10px hsl(26, 100%, 55%);
  border-radius: 10px;
  width: 18rem;
  color: white;
  border: none;
  margin-left: 1rem;
  background-color: hsl(26, 100%, 55%);
}
button path {
  fill: white;
}
.btn-icon {
  margin-right: 1rem;
}
button:hover {
  opacity: 0.55;
}
.cart-box {
  position: absolute;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.5);
  top: 5rem;
  right: 1rem;
  border-radius: 7px;
  height: 13.5rem;
  width: 21rem;
  background-color: white;
}
.black1 {
  margin: 1rem;
  font-weight: 700;
}
.line {
  height: 3px;
  width: 100%;
  background-color: hsl(223, 64%, 98%);
}

.cart-box button {
  margin: 1rem;
  width: calc(100% - 2rem);
}
.item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 2.8rem;
  margin: 1rem;
  margin-top: 1.2rem;
  margin-bottom: 1.5rem;
  width: calc(100% - 2rem);
}
.item img {
  border-radius: 5px;
  height: 100%;
}
.item p {
  margin-left: 0.7rem;
  color: hsl(219, 9%, 45%);
}
span {
  font-weight: 700;
  color: hsl(220, 13%, 13%);
}
.gray {
  width: 100%;
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  height: 8rem;
  font-weight: 700;
  color: hsl(219, 9%, 45%);
}
.mobile {
  display: none;
}
.none {
  display: none;
}
.price-left {
  display: flex;
  flex-direction: row;
  align-items: center;
}
@media (max-width: 700px) {
  .close {
    display: none;
  }
  .desktop {
    display: none;
  }
  .cart-box {
    z-index: 3;

    right: 0.7rem;
    top: 4.5rem;
  }
  .mobile {
    display: block;
  }
  .icon-menu {
    cursor: pointer;
    height: 1.1rem;
  }
  .logo {
    transform: translateY(-2px);
  }
  .nav-left {
    min-width: 0;
    max-width: 12.5rem;
    align-items: center;
    transform: scale(0.9);
  }
  .nav-right {
    transform: scale(0.9);
    min-width: 5rem;
  }
  main {
    padding: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  .lector {
    width: 100%;
  }
  .thumbnails {
    display: none;
  }
  .light-thumbnails {
    display: none;
  }
  .view {
    display: none;
  }
  .light-box {
    transform: none;
    top: 0;
    left: 0;
    position: relative;
    display: flex;
    flex-direction: row;
  }
  .light-view {
    aspect-ratio: 10 / 9;
    width: 100%;
    max-width: 26rem;
    object-fit: cover;
    border-radius: 0;
    margin: 0;
  }
  .arrow {
    width: 3rem;
    height: 3rem;
    top: 45%;
    transform: scale(0.8);
  }

  .prev {
    left: 0.75rem;
  }
  .next {
    right: 0.75rem;
  }
  nav {
    height: 4rem;
    border: none;
  }
  .avatar {
    width: 2rem;
  }
  .nav-links {
    display: none;
  }
  .details {
    max-width: 26rem;
    padding: 1.5rem;
    width: 100%;
  }
  .tittle {
    font-size: 2rem;
  }
  .desc {
    font-weight: 400;
    font-size: 0.85rem;
    line-height: 1.5rem;
  }
  .mobile.discount-off {
    margin: 0;
    float: right;
  }
  .price-box {
    justify-content: space-between;
  }

  .buy-box {
    margin-top: 1rem;
    flex-direction: column;
    align-items: center;
  }
  button {
    width: 100%;
    margin: 0;
  }
  .count-box {
    margin-bottom: 1rem;
    width: 100%;
  }
  .nav-links.show {
    display: flex;
    flex-direction: column;
    position: fixed;
    justify-content: flex-start;
    top: 0;
    left: 0;
    height: 100vh;
    width: 40%;
    min-width: 10rem;
    background-color: white;
    z-index: 5;
    color: black;
    font-weight: 700;
    padding: 4.5rem 1rem;
    text-align: left;
    font-size: 0.9rem;
  }
  .nav-links.show .nav-link {
    text-align: left;
    width: 100%;
    display: block;
    margin-bottom: 1rem;
  }
  .overlay-mobile.show {
    background-color: black;
    opacity: 0.75;
    position: absolute;
    top: 0;
    width: 100vw;
    height: 100vh;
    left: 0;
    z-index: 4;
  }
  .close-mobile {
    cursor: pointer;
    position: fixed;
    display: block;
    left: 1rem;
    top: 1rem;
  }
}
